import React, { useState, useEffect } from "react";
import {
  Layout,
  Row,
  Col,
  Form,
  Button,
  Input,
  Breadcrumb,
  DatePicker,
  message,
  Alert,
  Spin,
  Select
} from "antd";
import { NavLink } from "react-router-dom";
import { LoggedinHeader } from "../../containers";
import OrgSidebar from "../../containers/OrgSidebar";
import MultiSelect from "../managePolls/MultiSelect";
import FloatLabel from "../form/FloatLabels";
import RichTextEditor from "./Editor";
import momentUtil from "../../utils/momentUtil";
import moment from "moment";
import UploadBroadcast from "./UploadBroadcast";
import Location from "./Location";
import { IMAGE, DOCUMENT, SCHEDULE, CANCEL ,UPLOAD, MANAGE_BROADCAST_MESSAGE, CONFIGURE_CUG,  CREATE_BROADCAST_MESSAGE} from "../../headings";
import { REQUIRED_MESSAGE, UPLOAD_FORMAT_ERROR } from "../../errors";
import { UPLOAD_NOTE } from "../../messages";
import authHeader from "../../services/auth-header";
import axios from "axios";
import * as AppStorage from "../../utils/AppStorage";
import { BASE_URL } from "../../userconfig";
import "date-fns";
import { TimePicker } from "@progress/kendo-react-dateinputs";
import { TIME_ZONE, CLEAR_ALL } from "../../labels";
import getCUG from "../../actions/cug";
import { connect } from "react-redux";

const { Content } = Layout;
const { TextArea } = Input;

const CreateBroadcast = (props) => {
  const [broadcast, setBroadcast] = useState({
    messageTittle: "",
    meesage: "",
    startDate: "",
    startTime: new Date(),
    attachmentType: "",
    attachments: [],
    broadCastImage: [],
    broadCastDocument: [],
    previewVisible: false,
    previewImage: "",
    previewTitle: "",
    mapLoction: "",
  });
  const [uploadError, showuploadError] = useState(true);
  const [isImage, setIsImage] = useState(false);
  const [error, showError] = useState(false);
  const [disabledbtn, setDisabledbtn] = useState(false);
  const [loader, setLoader] = useState(false);
  const [broadcastErrorMessage, setBroadcastErrorMessage] = useState("");
  const [broadcastError, setBroadcastError] = useState(false);
  const [selectedTimeZone, setSelectedTimeZone] = useState("");
  const [itemRemoved, setItemRemoved] = useState("");
  const [cugDetails, setCugDetails] = useState({});
  


  const onSchedule = (e) => {
    e.preventDefault();
    if (
      uploadError &&
      broadcast.messageTittle &&
      broadcast.messageTittle.replace(/^\s*/, "").replace(/\s*$/, "").length >
        0 &&
      broadcast.meesage &&
      broadcast.startTime &&
      broadcast.startDate
    ) {
      scheduleBroadcast();
      showError(false);
    } else {
      showError(true);
    }
  };

  const scheduleBroadcast = () => {
    setLoader(true);
    const token = authHeader();
    const options = {
      headers: {
        Authorization: token,
        Accept: "application/json, text/plain",
        "Content-Type": "application/json;charset=UTF-8",
      },
    };

    let attachments = {};
    if (broadcast.mapLoction) {
      attachments = {
        documents: broadcast.broadCastDocument?.map((value) => {
          return { url: value.base64, name: value.name };
        }),
        images: broadcast.broadCastImage?.map((value) => {
          return { url: value.base64, name: value.name };
        }),
        location: broadcast.mapLoction,
      };
    } else {
      attachments = {
        documents: broadcast.broadCastDocument?.map((value) => {
          return { url: value.base64, name: value.name };
        }),
        images: broadcast.broadCastImage?.map((value) => {
          return { url: value.base64, name: value.name };
        }),
      };
    }

    let data = {};
    if (
      broadcast.broadCastDocument.length === 0 &&
      broadcast.broadCastImage.length === 0 &&
      !broadcast.mapLoction
    ) {
      data = {
        title: broadcast.messageTittle,
        message: broadcast.meesage,
        scheduled_date: momentUtil.formatAsDDMMYYYY(broadcast.startDate),
        scheduled_time: momentUtil.formatAsHHmmss(broadcast.startTime),
        scheduled_date_timezone: selectedTimeZone
      };
    } else {
      data = {
        title: broadcast.messageTittle,
        message: broadcast.meesage,
        attachments: attachments,
        scheduled_date: momentUtil.formatAsDDMMYYYY(broadcast.startDate),
        scheduled_time: momentUtil.formatAsHHmmss(broadcast.startTime),
        scheduled_date_timezone: selectedTimeZone
      };
    }
    if(cugDetails){
      let  cugObjectData = {};
      const getCUGOBJ =  Object.keys(cugDetails);
      getCUGOBJ && getCUGOBJ.forEach((Item, index)=>{
          const cugFields = cugDetails[Item];
          const spitItem = Item.split("_");
          const actualItemValue = spitItem[1];
          cugObjectData[actualItemValue] =  Object.values(cugFields);;
      })
      data.cug_filters = cugObjectData;
    }
    data.offset =  -(new Date().getTimezoneOffset());
    axios
      .post(BASE_URL + `broadcast/`, data, options)
      .then(() => {
        setLoader(false);
        window.location = "/orgcc/broadcastmessages";
      })
      .catch((error) => {
        if (error.message.includes("400")) {
          setBroadcastError(true);
          setBroadcastErrorMessage(error.response.data.title);
        }
        setLoader(false);
      });
  };

  const onCancelBroadcast = (e) => {
    window.location = "/orgcc/broadcastmessages";
  };

  const onBroadcastChange = (e) => {
    setBroadcast({ ...broadcast, [e.target.id]: e.target.value });
  };

  const onBroadMessageChange = (content, name) => {
    setBroadcast((prevState) => {
      return { ...prevState, [name]: content };
    });
  };

  const onChangeStartDate = (date, name) => {
    setBroadcast({ ...broadcast, [name]: date });
  };

  const disabledDate = (current) => {
    return current && current < moment().startOf("day");
  };

  // const onChangeStartTime = (time, name) => {
  //   const currentTime = new Date();
  //   if (currentTime > time) {
  //     setBroadcast({ ...broadcast, [name]: time })

  //     //this.setState({ endTimeError: true });
  //   } else {
  //     setBroadcast({ ...broadcast, [name]: time })
  //   }
  // };

  const onChangeStartTime = (event, name) => {
    const startTime = event.target.value;
    setBroadcast({ ...broadcast, [name]: startTime });
  };

  const checkDoucmentType = (fileType) => {
    const isImage = fileType.includes("image");
    if (isImage) {
      setIsImage(true);
    } else {
      setIsImage(false);
    }
  };

  const setLocation = (latitude, longitude, name) => {
    var lng = longitude.toString().substring(0, 9);
    var lat = latitude.toString().substring(0, 9);

    var obj = {
      location_latitude: parseFloat(lat),
      location_longitude: parseFloat(lng),
      address: name,
    };
    setBroadcast({ ...broadcast, mapLoction: obj });
  };

  const handleStepDocumentUpload = (value, name) => {
    chekMaxUpload();
    if (value.file.status === "removed") {
      showuploadError(true);
      chekMaxUpload(value.file.status);
    }
    let fileList = value.fileList;
    checkDoucmentType(value.file.type);
    fileList.forEach(function (file, index) {
      let reader = new FileReader();
      reader.onload = (e) => {
        file.base64 = e.target.result;
        file.response = "Uploaded successfully";
      };
      reader.readAsDataURL(file.originFileObj);
    });
    let docname = "broadCastDocument";
    setBroadcast({ ...broadcast, [docname]: fileList });
  };

  const chekMaxUpload = (status) => {
    let documentLength = broadcast.broadCastDocument.length;
    let imageLength = broadcast.broadCastImage.length;
    let docLength = imageLength + documentLength;
    if (status === "removed") {
      if (docLength - 1 === 5) {
        setDisabledbtn(true);
      } else {
        setDisabledbtn(false);
      }
    } else {
      if (docLength === 4) {
        setDisabledbtn(true);
      } else {
        setDisabledbtn(false);
      }
    }
  };

  const handleStepOneUpload = (value, name) => {
    chekMaxUpload();
    if (value.file.status === "removed") {
      showuploadError(true);
      chekMaxUpload(value.file.status);
    }
    let fileList = value.fileList;

    checkDoucmentType(value.file.type);
    fileList.forEach(function (file, index) {
      let reader = new FileReader();
      reader.onload = (e) => {
        file.base64 = e.target.result;
        file.response = "Uploaded successfully";
      };
      reader.readAsDataURL(file.originFileObj);
    });
    setBroadcast({ ...broadcast, [name]: fileList });
  };

  const handlePollImagePreview = async (file) => {
    file.preview = await getBase64(file.originFileObj);
    setBroadcast({
      ...broadcast,
      previewImage: file.url || file.preview,
      previewVisible: true,
      previewTitle:
        file.name || file.url.substring(file.url.lastIndexOf("/") + 1),
    });
  };

  const handleCancel = (value) =>
    setBroadcast({ ...broadcast, previewVisible: false });

  const getBase64 = (file) => {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => resolve(reader.result);
      reader.onerror = (error) => reject(error);
    });
  };

  const handleOnRemove = (value, name) => {
    // setPoll({ ...poll, pollImageValidation: true });
  };

  const handleBeforeDocUpload = (file, name) => {
    let xlsxfileExt = file.name.includes(".xls");
    let pptxfileExt = file.name.includes(".pptx");
    let pptfileExt = file.name.includes(".ppt");
    let docfileExt = file.name.includes(".doc");
    let csvfileExt = file.name.includes(".csv");

    const isValidDoc =
      file.type === "application/pdf" ||
      xlsxfileExt ||
      pptxfileExt ||
      pptfileExt ||
      docfileExt ||
      csvfileExt;
    if (!isValidDoc) {
      showuploadError(false);
      message.error(`${file.name} is not a supported format file type.`);
    } else {
      showuploadError(true);
    }

    const isLt10M = file.size / 1024 / 1024 < 10;
    if (isValidDoc) {
      if (!isLt10M && isValidDoc) {
        showuploadError(false);
        message.error("Document must smaller than 10MB!");
      } else {
        showuploadError(true);
      }
    }

    return isValidDoc && isLt10M;
  };

  const handleBeforeUpload = (file, name) => {
    const isImage =
      file.type === "image/jpeg" ||
      file.type === "image/png" ||
      file.type === "image/gif";

    const isLt5M = file.size / 1024 / 1024 < 10;
    const isLt10M = file.size / 1024 / 1024 < 10;

    if (!isImage) {
      showuploadError(false);
      message.error(`${file.name} is not a supported format file type.`);
    } else {
      showuploadError(true);
    }
    if (isImage) {
      if (!isLt5M && isImage) {
        showuploadError(false);
        message.error("Image must smaller than 5MB!");
      } else {
        showuploadError(true);
      }
    }

    return isImage && isLt5M;
  };

  const getDisabledHours = (value) => {
    var hours = [];
    if (moment().isSame(broadcast.startDate, "day")) {
      for (var i = 0; i < moment().hour(); i++) {
        hours.push(i);
      }
    }
    return hours;
  };

  const getDisabledMinutes = (selectedHour) => {
    var minutes = [];
    if (moment().isSame(broadcast.startDate, "day")) {
      if (selectedHour === moment().hour()) {
        for (var i = 0; i < moment().minute(); i++) {
          minutes.push(i);
        }
      }
    }
    return minutes;
  };

  const getDisabledSeconds = (selectedHour, selectedMinute) => {
    var second = [];
    if (
      selectedHour === moment().hour() &&
      selectedMinute == moment().minute()
    ) {
      for (var i = 0; i < moment().second(); i++) {
        second.push(i);
      }
    }
    return second;
  };

  useEffect(() => {
    const { dispatch } = props;
    dispatch(getCUG());
  }, []);

  useEffect(() => {
    if (props.cug !== null) {
      let values = {};
      Object.keys(props.cug.cug_field).forEach((value) => {
        values = { ...values, [value]: [] };
      });
      setCugDetails(values);
    }
  }, [props.cug]);
  

   const handleOnCUGSelect = (name, value) => {
    setCugDetails({
        ...cugDetails,
        [name]: { ...cugDetails[name], ...value },
      });

      console.log({
        ...cugDetails,
        [name]: { ...cugDetails[name], ...value },
      });
   }
  

  // const itemRemoved = (value) => {
  //   setItemRemoved(value);
  // };

  const removeValues = (value) => {
    setItemRemoved(value);
  };
console.log("-----props.cug----", cugDetails)
  return (
    <React.Fragment>
      {" "}
      <Row className="inner-header">
        <LoggedinHeader amORGUser />
      </Row>
      <Row className="content-center pt-0">
        <Layout>
          <Layout>
            <OrgSidebar amORGUser data={props} />
            <Content>
              <Row className="pt-3">
                <Breadcrumb separator=">">
                  <NavLink to="/orgcc/broadcastmessages">
                    <Breadcrumb.Item>{MANAGE_BROADCAST_MESSAGE}</Breadcrumb.Item>
                  </NavLink>
                  <Breadcrumb.Item>{CREATE_BROADCAST_MESSAGE}</Breadcrumb.Item>
                </Breadcrumb>
              </Row>

              <Spin spinning={loader} size="large">
                <Row className="py-3">
                  <h1 className="mb-0">{CREATE_BROADCAST_MESSAGE}</h1>
                </Row>
                <div className="mid-content">
                  <Row>
                    <Col span={24}>
                      <Form
                        className="input-md floating-label w-100"
                        name="validate_other"
                      >
                        {error && (
                          <Form.Item>
                            <Alert
                              message={REQUIRED_MESSAGE}
                              type="error"
                              showIcon
                            ></Alert>
                          </Form.Item>
                        )}

                        {broadcastError && (
                          <Form.Item>
                            <Alert
                              message={broadcastErrorMessage}
                              type="error"
                              showIcon
                            ></Alert>
                          </Form.Item>
                        )}
                        <Col span={24}>
                          <Row>
                            <Col span={24} md={18} className="gutter-row">
                              <FloatLabel
                                label={"Message Title"}
                                value={broadcast.messageTittle}
                              >
                                <Form.Item
                                  className="required"
                                  name="message title"
                                  validateStatus="success"
                                >
                                  <TextArea
                                    showCount
                                    className="input-control"
                                    id="messageTittle"
                                    name="messageTittle"
                                    htmlFor="helptext"
                                    maxLength={80}
                                    value={broadcast.messageTittle}
                                    autoComplete={"off"}
                                    onChange={(e) => {
                                      onBroadcastChange(e);
                                    }}
                                  />
                                </Form.Item>
                              </FloatLabel>
                            </Col>
                          </Row>
                        </Col>
                        <Col span={24}>
                          <Row>
                            <Col span={24} md={18} className="gutter-row">
                              <FloatLabel
                                label="Message"
                                value={broadcast.message}
                              >
                                <Form.Item
                                  className="required"
                                  name="message"
                                  validateStatus="success"
                                >
                                  <RichTextEditor
                                    value={broadcast.meesage}
                                    onChange={(content) =>
                                      onBroadMessageChange(content, "meesage")
                                    }
                                    id="message"
                                  />
                                </Form.Item>
                              </FloatLabel>
                            </Col>
                          </Row>
                        </Col>
                        <Col span={24} className="gutter-row">
                          <Row
                            className="align-items-center d-flex"
                            gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}
                          >
                            <Col span={24} md={9} lg={6} className="gutter-row">
                              <FloatLabel label="Schedule Date" value="date">
                                <Form.Item
                                  className="required mb-0"
                                  name="date"
                                >
                                  <DatePicker
                                    value={broadcast.startDate}
                                    disabledDate={disabledDate}
                                    onChange={(date) =>
                                      onChangeStartDate(date, "startDate")
                                    }
                                  />
                                </Form.Item>
                              </FloatLabel>
                            </Col>
                            <Col span={24} md={9} lg={6} className="gutter-row">
                              <FloatLabel label="Schedule Time" value="date">
                                <Form.Item
                                  className="required timepicker mb-0"
                                  name="time"
                                >
                                  <>
                                    <TimePicker
                                      value={broadcast.startTime}
                                      onChange={(value) =>
                                        onChangeStartTime(value, "startTime")
                                      }
                                    />
                                  </>
                                </Form.Item>
                              </FloatLabel>
                            </Col>
                            <Col span={24} md={9} lg={6} className="gutter-row">
                            <FloatLabel label={TIME_ZONE} value={selectedTimeZone}>
                        <Form.Item className="required mb-0" name="scheduled_date_timezone">
                          <Select
                            className=""
                            htmlFor="scheduled_date_timezone"
                            onSelect={(value) =>
                              setSelectedTimeZone(value)
                            }
                            defaultValue={selectedTimeZone}
                          >
                            {moment.tz.names().map((value) => (
                              <Select.Option value={value}>
                                {value}
                              </Select.Option>
                            ))}
                          </Select>
                        </Form.Item>
                      </FloatLabel>
                            </Col>
                            <Col span={24} className="6gutter-row mt-3">
                            {props.cug && Object.keys(props.cug?.cug_field).length > 0 ? (
              <>
                <h6 className="mb-4">{CONFIGURE_CUG}</h6>
                {Object.keys(props.cug.cug_field).map((item, key) => {
                  return (
                    <Row gutter={{ xs: 8, sm: 16, md: 24, lg: 32 }}>
                      <Col span={24} lg={5} className="gutter-row">
                        <FloatLabel label={item}></FloatLabel>
                      </Col>
                      <Col span={24} lg={5} className="gutter-row">
                        <Form.Item className="mb-0" name={item}>
                          <MultiSelect
                            cugData={props.cug?.cug_field}
                            pollCug={cugDetails}
                            item={item}
                            handleOnCUGSelect={handleOnCUGSelect}
                            itemRemoved={itemRemoved}
                          ></MultiSelect>
                        </Form.Item>
                      </Col>
                      <Col
                        span={4}
                        lg={2}
                        className="gutter-row d-flex align-items-center mt-2 mt-lg-0"
                      >
                        <Button onClick={() => removeValues(item)}>
                          {CLEAR_ALL}
                        </Button>
                      </Col>
                    </Row>
                  );
                })}
              </>
            ) : null}
                            </Col>
                            <Col span={24} className="6gutter-row mt-3">
                              {!uploadError && (
                                <Form.Item>
                                  <Alert
                                    message={UPLOAD_FORMAT_ERROR}
                                    type="error"
                                    showIcon
                                  ></Alert>
                                </Form.Item>
                              )}
                              <h6>{UPLOAD}</h6>
                              <span>{UPLOAD_NOTE} </span>
                              <Form.Item
                                name="help text"
                                className="upload-list mt-3"
                              >
                                <Row>
                                  <UploadBroadcast
                                    file={broadcast.broadCastImage}
                                    handleStepOneUpload={handleStepOneUpload}
                                    handleBeforeUpload={handleBeforeUpload}
                                    handleOnRemove={handleOnRemove}
                                    handlePollImagePreview={
                                      handlePollImagePreview
                                    }
                                    handleCancel={handleCancel}
                                    broadcast={broadcast}
                                    uploadtitle={IMAGE}
                                    disabledbutton={disabledbtn}
                                  />
                                  <UploadBroadcast
                                    file={broadcast.broadCastDocument}
                                    handleStepOneUpload={
                                      handleStepDocumentUpload
                                    }
                                    handleBeforeUpload={handleBeforeDocUpload}
                                    handleOnRemove={handleOnRemove}
                                    handlePollImagePreview={
                                      handlePollImagePreview
                                    }
                                    handleCancel={handleCancel}
                                    broadcast={broadcast}
                                    uploadtitle={DOCUMENT}
                                    disabledbutton={disabledbtn}
                                  />
                                  <Location
                                    setLocation={setLocation}
                                    disabledLocation={
                                      disabledbtn ? true : false
                                    }
                                    locationName={broadcast.mapLoction.address}
                                  />
                                </Row>
                              </Form.Item>
                            </Col>
                          </Row>
                          <div className="mt-5">
                            <Button
                              type="secondary"
                              type="secondary"
                              htmlType="submit"
                              className="md mr-4"
                              onClick={onCancelBroadcast}
                            >
                              {CANCEL}
                            </Button>
                            <Button type="primary" onClick={onSchedule}>
                              {SCHEDULE}
                            </Button>
                          </div>
                        </Col>
                      </Form>
                    </Col>
                  </Row>
                </div>
              </Spin>
            </Content>
          </Layout>
        </Layout>
      </Row>
    </React.Fragment>
  );
};


function mapStateToProps(state) {
  const { cug } = state.cug;
  return {
    cug,
  };
}

export default connect(mapStateToProps)(CreateBroadcast);
